import { json, LoaderFunction } from "@remix-run/node";
import { executeQuery } from "./database";
import bcrypt from "bcryptjs";
import Cookies from 'js-cookie';

export let action: LoaderFunction = async ({ request }) => {
if (request.method === "GET") {
    const existingCookie = request.headers.get("Cookie");
    if (existingCookie) {
      // Redirect to profile page if the user is already logged in
      return json(
        { message: "User already logged in" },
        { status: 302, headers: { Location: "/profile" } }
      );
    }
  }

  if (request.method === "POST") {
    const formData = new URLSearchParams(await request.text());
    const username = formData.get("username");
    const password = formData.get("password");

    const query = `SELECT * FROM comp6000_06.Customer WHERE email = ?`;

    try {
      const result = await executeQuery(query, [username]);

      if (result.length > 0) {
        const user = result[0];
        const passwordMatch =
          password !== null && user.password !== null
            ? bcrypt.compare(password, user.password)
            : false;

        if (passwordMatch) {
          // Set a cookie with the user's email
          const setCookieHeader = `email=${encodeURIComponent(
            user.email
          )}; HttpOnly:false; Path=/; Max-Age=3600;`; // Adjust Max-Age as needed
          return json(
            { message: "Login successful" },
            {
              status: 302,
              headers: {
                Location: "/profile",
                "Set-Cookie": setCookieHeader,
              },
            }
          );
        } else {
          return json(
            { error: "Invalid username or password" },
            { status: 401 }
          );
        }
      } else {
        return json(
          { error: "Invalid username or password" },
          { status: 401 }
        );
      }
    } catch (error) {
      console.error(error);
      return json({ error: "Database error" }, { status: 500 });
    }
  }

  // Return an empty response for GET requests
  return json({});
};
