import { cssBundleHref } from "@remix-run/css-bundle";
import type { LinksFunction } from "@remix-run/node";
import stylesheet from "~/tailwind.css";
import { MagnifyingGlassIcon, ShoppingBagIcon, UserIcon, MapPinIcon } from "@heroicons/react/24/outline";
import { Links, LiveReload, Meta, NavLink, Outlet, Scripts, ScrollRestoration } from "@remix-run/react";
import React, { useState } from "react";
export const links: LinksFunction = () => [
...(cssBundleHref ?
[
{ rel: "stylesheet", href: cssBundleHref },
{ rel: "stylesheet", href: stylesheet }] :

[{ rel: "stylesheet", href: stylesheet }])];


const navigation = {
  support: [
  { name: "Contact Us", href: "/contact" },
  { name: "Services", href: "/service" }],

  company: [
  { name: "About Us", href: "/about" },
  { name: "Locations", href: "/locations" },
  { name: "Account", href: "/login" }],

  legal: [
  { name: "FAQs", href: "/faq" },
  { name: "Terms and Conditions", href: "#" },
  { name: "Privacy Notice", href: "#" }]

};

export default function App() {
  const [searchInput, setSearchInput] = useState("");
  const [searchResults, setSearchResults] = useState([]);

  const handleSearch = (e: {target: {value: React.SetStateAction<string>;};}) => {
    setSearchInput(e.target.value);

    // Representing pages and their types
    const pages = [
    { name: "Home", type: "Page" },
    { name: "About", type: "Page" },
    { name: "Service", type: "Category", subheadings: ["MOT", "Car Servicing", "Oil and BrakeRepairs", "TiresFix And Replacements", "Windscreen Repairs", "Battery Change", "Diagnostic Check", "Engine TuneUps"] },
    { name: "Contact", type: "Page" }];


    // Separate arrays for main category names and subheadings
    const mainCategories = pages.filter((page) => page.type === "Category" && !page.subheadings).map((page) => page.name);
    const subheadings = pages.filter((page) => page.type === "Category" && page.subheadings).flatMap((page) => page.subheadings);

    // Filter pages based on search input
    const filteredMainCategories = mainCategories.filter((name) => name.toLowerCase().includes(e.target.value.toLowerCase()));

    const filteredSubheadings = subheadings.filter((subheading) => subheading.toLowerCase().includes(e.target.value.toLowerCase()));

    const filteredResults = [...filteredSubheadings.map((subheading) => `${subheading} - Service`), ...filteredMainCategories];

    setSearchResults(filteredResults);
  };

  return (
    <html lang="en">
      <head>
        <meta charSet="utf-8" />
        <meta name="viewport" content="width=device-width,initial-scale=1" />
        <Meta />
        <Links />
      </head>

      <body className="flex flex-col min-h-screen " style={{ backgroundColor: "#F9F7F7" }}>
        <div className="flex flex-col min-h-screen">

          {/* Navigation Bar */}
          <nav className="fixed inset-x-0 px-6 py-3 flex flex-row justify-between top-0 mx-auto w-full" style={{ backgroundColor: "#F9F7F7", borderBottom: "5px solid #112D4E", zIndex: "1000" }}>
            {/* Logo */}
            <div className="pt-5">{/*<img src="/images/logo.png" alt="Logo" />*/}</div>

            {/* Navigation Items */}
            <ul className=" nav flex space-x-8 pt-5 pl-20 justify-center align-items-center w-full font-bold">
              <NavLink to={"/"} className={({ isActive }) => `${isActive ? "text-blue-800" : ""}`}>
                Home
              </NavLink>
              <NavLink to={"/about"} className={({ isActive }) => `${isActive ? "text-blue-800" : ""}`}>
                About Us
              </NavLink>
              <NavLink to={"/service"} className={({ isActive }) => `${isActive ? "text-blue-800" : ""}`}>
                Services
              </NavLink>
              <NavLink to={"/contact"} className={({ isActive }) => `${isActive ? "text-blue-800" : ""}`}>
                Contact Us
              </NavLink>
              <div className="flex-grow"></div> {/* Use for remaining space */}
              <div className="flex space-x-4 items-center">

                {/* Search Bar */}
                <div>
                  <form className="flex">
                    <input type="text" placeholder="Search" style={{ width: "200px" }} value={searchInput} onChange={handleSearch} />

                    <button style={{ backgroundColor: "#112D4E", color: "#F9F7F7" }} onClick={handleSearch}>
                      <MagnifyingGlassIcon className="h-6 w-6" style={{ width: "50px" }} />
                    </button>
                  </form>

                  {/* Display search results */}
                  {searchResults.length > 0 &&
                  <div className="absolute mt-2 bg-white border rounded-md shadow-md">
                    <ul>
                      {searchResults.map((result, index) =>
                      <li key={index} className="py-2 px-4 hover:bg-gray-200">
                          {result.includes(' - ') ?
                        <NavLink to={`/${result.split(' - ')[0].toLowerCase().replace(/\s+/g, '')}`} className="text-blue-800">
                              {result}
                            </NavLink> :

                        <NavLink to={`/${result === 'About Us' ? 'about' : result.toLowerCase().replace(/\s+/g, '')}`} className="text-blue-800">
                              {result}
                            </NavLink>}
                        
                        </li>
                      )}
                

                      </ul>
                    </div>}
                  
                </div>

                <NavLink to={"/locations"} className={({ isActive }) => `${isActive ? "text-blue-800" : ""}`}>
                  <div className="flex flex-col items-center">
                    <MapPinIcon className="h-6 w-6" />
                    <span style={{ fontSize: "10px", fontWeight: "lighter" }}>Locations</span>
                  </div>
                </NavLink>

                <NavLink to={"/login"} className={({ isActive }) => `${isActive ? "text-blue-800" : ""}`}>
                  <div className="flex flex-col items-center">
                    <UserIcon className="h-6 w-6" />
                    <span style={{ fontSize: "10px", fontWeight: "lighter" }}>Account</span>
                  </div>
                </NavLink>

                <NavLink to={"/basket"} className={({ isActive }) => `${isActive ? "text-blue-800" : ""}`}>
                  <div className="flex flex-col items-center">
                    <ShoppingBagIcon className="h-6 w-6" />
                    <span style={{ fontSize: "10px", fontWeight: "lighter" }}>Basket</span>
                  </div>
                </NavLink>
              </div>
            </ul>
          </nav>
          <style>
            {`
  .nav {
    text-transform: uppercase;
    text-align: center;
    font-weight: 600;
  }
         

  .nav * {
    box-sizing: border-box;
    transition: all .35s ease;
  }

  .nav a {
    display: inline-block;
    list-style: outside none none;
    padding: 0.5em 0.8em;
    color: #112D4E;
    position: relative;
    text-decoration: none;
    font-size: 20px;
  }

  .nav a::before,
  .nav a::after {
    content: '';
    height: 12px;
    width: 12px;
    position: absolute;
    transition: all .35s ease;
    opacity: 0;
  }

  .nav a::before {
    right: 0;
    top: 0;
    border-top: 3px solid #DBE2EF;
    border-right: 3px solid #3F72AF;
    transform: translate(-100%, 50%);
  }

  .nav a:after {
    left: 0;
    bottom: 0;
    border-bottom: 3px solid #DBE2EF;
    border-left: 3px solid #3F72AF;
    transform: translate(100%, -50%);
  }

  .nav a:hover:before,
  .nav a:hover:after,
  .nav a.text-blue-800:before,
  .nav a.text-blue-800:after {
    transform: translate(0, 0);
    opacity: 1;
  }

  .nav a:hover {
    color: #3F72AF;
  }
`}
          </style>

          {/* The rest of your component */}
          <div className="flex-grow">
            <Outlet />
          </div>

          <footer className=" " aria-labelledby="footer-heading" style={{ marginTop: "150px", backgroundColor: "#F9F7F7", borderTop: "5px solid #112D4E" }}>
            <h2 id="footer-heading" className="sr-only">
              Footer
            </h2>
            <div className="mx-auto max-w-6xl px-6 pb-8 pt-16 sm:pt-24 lg:px-8 lg:pt-32">
              <div className="grid grid-cols-1 md:grid-cols-4 gap-8">
                <div className="mb-16 md:mb-0">
                  <h3 className="text-sm font-semibold leading-6 text-gray-900">Support</h3>
                  <ul role="list" className="mt-2 space-y-2">
                    {navigation.support.map((item) =>
                    <li key={item.name}>
                        <a href={item.href} className="text-sm leading-6 text-gray-600 hover:text-gray-900">
                          {item.name}
                        </a>
                      </li>
                    )}
                  </ul>
                </div>

                <div className="mb-16 md:mb-0">
                  <h3 className="text-sm font-semibold leading-6 text-gray-900">Company</h3>
                  <ul role="list" className="mt-2 space-y-2">
                    {navigation.company.map((item) =>
                    <li key={item.name}>
                        <a href={item.href} className="text-sm leading-6 text-gray-600 hover:text-gray-900">
                          {item.name}
                        </a>
                      </li>
                    )}
                  </ul>
                </div>

                <div>
                  <h3 className="text-sm font-semibold leading-6 text-gray-900">Legal</h3>
                  <ul role="list" className="mt-2 space-y-2">
                    {navigation.legal.map((item) =>
                    <li key={item.name}>
                        <a href={item.href} className="text-sm leading-6 text-gray-600 hover:text-gray-900">
                          {item.name}
                        </a>
                      </li>
                    )}
                  </ul>
                </div>

                <div className="">
                  <h3 className="text-sm font-semibold leading-6 text-gray-900">Subscribe to our newsletter</h3>
                  <p className="mt-2 text-sm leading-6 text-black-300">The latest news, articles, and resources, sent to your inbox weekly.</p>
                  <form className="mt-6 sm:flex sm:max-w-md">
                    <label htmlFor="email-address" className="sr-only">
                      Email address
                    </label>
                    <input
                      type="email"
                      name="email-address"
                      id="email-address"
                      autoComplete="email"
                      required
                      className="min-w-0 e border-1 px-2 py-1 w-60 bg-white/5 text-base text-black ring-1 ring-inset ring-white/10 placeholder:text-gray-500 focus:ring-2 focus:ring-inset focus:ring-indigo-900 sm:w-64 sm:text-sm sm:leading-6 xl:w-full"
                      placeholder="Enter your email"
                      style={{ borderColor: "#112D4E" }} />
                    
                    <div className="mt-4 sm:ml-4 sm:mt-0 sm:flex-shrink-0">
                      <button type="submit" className="flex w-full items-center justify-center rounded-full px-3 py-2 text-sm font-light text-white " style={{ backgroundColor: "#112D4E", fontSize: "16px" }}>
                        Subscribe
                      </button>
                    </div>
                  </form>
                </div>
              </div>
            </div>
          </footer>
        </div>

        <>
          <ScrollRestoration />
          <Scripts />
          <LiveReload />
        </>
      </body>
    </html>);

}