import { useEffect, useState } from "react";
import { Link, useNavigate } from 'react-router-dom';
import { UserGroupIcon } from "@heroicons/react/24/outline";
import Cookies from "js-cookie";

function classNames(...classes: string[]) {
  return classes.filter(Boolean).join(" ");
}



  function profilecookie() {
    if (Cookies.get("email")!=null) {
      window.location.replace("/profile");
    }
  }

  export default function Login() {
    useEffect(() => {
      profilecookie();
    }, []);

    
  
  
   return (

    <div>
      <div className="flex min-h-full flex-1 flex-col justify-center px-6 py-12 lg:px-8">
        <div className="sm:mx-auto sm:w-full sm:max-w-sm">
          {/* Page Title */}
          <h2 className="mt-10 text-center text-2xl font-bold leading-9 tracking-tight pt-10" style={{ color: "#112D4E" }}>
            Sign in to your account
          </h2>
        </div>

        <div className="mt-10 sm:mx-auto sm:w-full sm:max-w-sm">
        <form className="space-y-6" action="/loginDB" method="POST">
            {/* Username/Email Input */}
            <div>
              <label htmlFor="username" className="block text-sm font-medium leading-6 text-gray-900" style={{ fontSize: "16px", color: "#112D4E" }}>
                 Email
              </label>
              <div className="mt-2">
                <input id="username" name="username" type="text" required className="block w-full border-0 py-1.5 text-gray-900 shadow-sm ring-1 focus:ring-indigo-950 ring-gray-300 placeholder:text-gray-400 focus:ring-2 focus:ring-inset sm:text-sm sm:leading-6" style={{ fontSize: "16px", borderColor: "#112D4E" }} />
              </div>
            </div>

            {/* Password Input */}
            <div>
              <label htmlFor="password" className="block text-sm font-medium leading-6 text-gray-900" style={{ fontSize: "16px", color: "#112D4E" }}>
                Password
              </label>
              <div className="mt-2">
                <input id="password" name="password" type="password" autoComplete="current-password" required className="block w-full border-0 py-1.5 text-gray-900 shadow-sm ring-1 focus:ring-indigo-950 ring-gray-300 placeholder:text-gray-400 focus:ring-2 focus:ring-inset sm:text-sm sm:leading-6" />
              </div>
            </div>

            {/* Forgot Password Link */}
            <div>
              <Link to="/forgotPassword" className="block text-sm font-medium leading-6 text-indigo-900" style={{ fontSize: "16px", color: "#112D4E" }}>
                Forgot your password?
              </Link>
            </div>

            {/* Log In Button */}
            <div>
              <button
                type="submit"
                className="flex w-full justify-center rounded-full px-3 py-1.5 text-sm font-semibold leading-6 text-white shadow-sm focus-visible:outline focus-visible:outline-2 focus-visible:outline-offset-2 focus-visible:outline-indigo-950"
                style={{ backgroundColor: "#112D4E", fontSize: "16px" }}
              >
                Sign in
              </button>
              
            </div>
          </form>

          {/* Sign Up */}
          <p className="mt-6 text-center text-sm text-gray-500">
            Don't have an account?{" "}
            <Link to="/signup" className="font-semibold leading-6" style={{ color: "#112D4E" }}>
              Sign Up
            </Link>
          </p>

          {/* Staff Login */}
          <p className="mt-6 text-center text-sm text-gray-500">
          <Link to="/staffLogin" className="font-semibold leading-6" style={{ color: "#112D4E" }}>
          <div className="flex flex-col items-center">
              <UserGroupIcon className="h-6 w-6" />
              <span style={{ fontSize: "10px", fontWeight: "lighter" }}>Staff Login</span>
            </div>
            </Link>
          </p>
        </div>
      </div>
    </div>
  );}
