import React, { ChangeEvent, SetStateAction, useState } from "react";
import DatePicker from "react-datepicker";
import "react-datepicker/dist/react-datepicker.css?__remix_sideEffect__";
import Breadcrumbs from './breadcrumbs';

export default function OilAndFilter() {

  const [registration, setRegistration] = useState("");
  const [postCode, setPostCode] = useState("");
  const [selectedDate, setSelectedDate] = useState<Date | null>(null);

  const breadcrumbPaths = [
  { label: 'Services', url: '/service' },
  { label: 'Car Servcing', url: '/carServicing' },
  { label: 'Oil And filter Service', url: '/oilAndFilter' }];


  const handleRegistrationChange = (e: ChangeEvent<HTMLInputElement>) => {
    setRegistration(e.target.value);
  };

  const handlePostCodeChange = (e: ChangeEvent<HTMLInputElement>) => {
    setPostCode(e.target.value);
  };

  const handleDateChange = (date: SetStateAction<Date | null>) => {
    setSelectedDate(date);
  };

  const handleSubmit = (e: React.FormEvent<HTMLFormElement>) => {
    e.preventDefault();
  };

  return (
    <div className="flex items-center justify-center h-screen">
      {/* Breadcrumbs */}
      <div className="fixed inset-x-0 px-6 py-3 flex flex-row justify-between mx-auto w-full top-28 pl-18">
        <Breadcrumbs paths={breadcrumbPaths} />
      </div>
      <div className="mt-16 max-w-md p-6 bg-white rounded shadow-md">

            {/* Navigation Bar */}
            <nav className="fixed inset-x-0 px-6 py-3 flex flex-row justify-between top-0 max-w-7xl mx-auto z-10" style={{ backgroundColor: '#F9F7F7' }}>
                {/* ... navigation content ... */}
            </nav>

        {/* Page  Content  */}    

        {/* Header */}
        <h1 className="text-3xl font-bold mb-4 text-center">Oil and Filter Change</h1>

        {/* Image */}
        <img
          src="/images/oil-and-filter.png" // Replace with the actual path to your image
          alt="Service Image"
          className="mx-auto mb-4 rounded-lg"
          style={{ maxWidth: '150px', height: 'auto' }} />
        

        {/* Service Details */}
        <p> 
         <span className="font-bold">From £109.99*</span> 
        </p>
        
        <p>*Prices vary by location and vehicle. Add your details below to get your personalized price</p>

        {/* Form */}
        <form onSubmit={handleSubmit} className="mt-4">
          {/* Registration Input */}
          <label htmlFor="registration" className="block mb-2 text-sm text-gray-700">
            <span className="text-navy-blue-500 font-bold">Registration:</span>
            <input type="text" id="registration" name="registration" className="border p-2 w-full mt-1" placeholder="Enter Registration" onChange={handleRegistrationChange} />
          </label>

          {/* Post Code Input */}
          <label htmlFor="postCode" className="block mb-2 text-sm text-gray-700">
            <span className="text-navy-blue-500 font-bold">Post Code:</span>
            <input type="text" id="postCode" name="postCode" className="border p-2 w-full mt-1" placeholder="Enter Post Code" onChange={handlePostCodeChange} />
          </label>

          {/* Calendar */}
          {registration && postCode &&
          <div className="mb-4">
              <label className="block mb-2 text-sm text-gray-700">
                <span className="text-navy-blue-500 font-bold">Select Date:</span>
                <DatePicker selected={selectedDate} onChange={handleDateChange} dateFormat="dd/MM/yyyy" />
              </label>
            </div>}
          

          {/* Book Now Button */}
          <button
            type="submit"
            className={`bg-blue-900 text-white px-4 py-2 rounded hover:bg-blue-700 ${!(selectedDate && registration && postCode) ? "opacity-50 cursor-not-allowed" : ""}`}
            disabled={!(selectedDate && registration && postCode)}>
            
            Book Now
          </button>
        </form>
        {/* other page content */}
        </div>
        </div>);

}