import { WrenchIcon } from "@heroicons/react/24/outline";
import { Link } from "@remix-run/react";
import React from "react";

const tires = [
  { href: "#", description: "Tires Fix And Replacements", icon: <WrenchIcon className="h-20 w-20 mt-8" style={{ color: "#112D4E" }} /> },
];

export default function TiresFixAndReplacements() {
  return (
    <div className="mt-16">
      {/* Page Content */}
      <div className="container mx-auto p-8 flex flex-col items-center justify-center">
        <h1 className="mt-2 text-4xl font-bold tracking-tight sm:text-6xl mb-9" style={{ color: "#112D4E" }}>
          Tire Fix and Replacement
        </h1>
        {/* Service Options */}
        <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-8">
          {/* Puncture Repair */}
          <div className="p-4 border rounded-md">
            <h2 className="text-xl font-bold mb-2">Puncture Repair</h2>
            <p>Quick fix for minor punctures</p>
            <p className="" style={{ color: "#3F72AF" }}>
              Starting from £24.99
            </p>
            <div className="flex items-center mt-2">
              <span className="text-gray-500 mr-1">432 views today</span>
              <svg className="w-4 h-4 fill-current text-blue-500" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 24 24">
                <path d="M0 0h24v24H0z" fill="none" />
                <path d="M12 2C6.48 2 2 6.48 2 12s4.48 10 10 10 10-4.48 10-10S17.52 2 12 2zm-1 17h2v2h-2zm0-14h2v10h-2z" />
              </svg>
            </div>
            <button className="text-white px-4 py-2 rounded mt-2" style={{ backgroundColor: "#112D4E" }}>
              <Link to  = "/punctureRepair">
              View Details
              </Link>
            </button>
          </div>

          {/* Tire Replacement */}
          <div className="p-4 border rounded-md">
            <h2 className="text-xl font-bold mb-2">Tire Replacement</h2>
            <p>High-quality tire replacement services</p>
            <p className="" style={{ color: "#3F72AF" }}>
              Starting from £59.99
            </p>
            <div className="flex items-center mt-2">
              <span className="text-gray-500 mr-1">871 views today</span>
              <svg className="w-4 h-4 fill-current text-blue-500" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 24 24">
                <path d="M0 0h24v24H0z" fill="none" />
                <path d="M12 2C6.48 2 2 6.48 2 12s4.48 10 10 10 10-4.48 10-10S17.52 2 12 2zm-1 17h2v2h-2zm0-14h2v10h-2z" />
              </svg>
            </div>
            <button className=" text-white px-4 py-2 rounded mt-2" style={{ backgroundColor: "#112D4E" }}>
            <Link to  = "/tireReplacement">
              View Details
              </Link>
            </button>
          </div>

        </div>

        {/* Warranty Information */}
        <div id="warranty">
          <ul>
            {tires.map((item, index) => (
              <li key={index}>
                <a href={item.href}>
                  <div className="flex flex-col items-center">
                    <span>{item.icon}</span>
                    <p className="font-bold text-2xl" style={{ color: "#112D4E" }}>{item.description}</p>
                    <p className="text-s" style={{ color: "#3F72AF", textAlign: "justify" }}>All tire fixes and replacements are backed by a satisfaction guarantee, providing you with peace of mind.</p>
                  </div>
                </a>
              </li>
            ))}
          </ul>
        </div>

        {/* Booking Information */}
        <div className="container mx-auto p-8 justify-center">
          {/* Booking Section Content */}
          {/* ... (Similar to the MOT booking section) */}
        </div>
      </div>
    </div>
  );
}
