import React, { useState, useEffect } from 'react';
import { Link, useParams } from '@remix-run/react';

export default function selectCentre() {
  const [showNearbyCentres, setShowNearbyCentres] = useState(false);
  const [selectedCentre, setSelectedCentre] = useState(null);
  const { postcode: urlPostcode, registration: urlRegistration } = useParams();
  const [postcode, setPostcode] = useState(urlPostcode || '');
  const [registration, setRegistration] = useState(urlRegistration || '');

  useEffect(() => {
    // This effect will run when the component mounts and when the URL parameters change
    console.log('Postcode:', postcode);
    console.log('Registration:', registration);
  }, [postcode, registration]);

  const nearbyCentres = [
    { name: 'Canterbury', address: 'University of Kent', city: 'Canterbury', county: 'Kent', country: 'United Kingdom', postcode: 'CT2 7FN', distance: 'Approx 0.5 miles away', tel: 'Tel: 01227 764000' },
    { name: 'Medway', address: 'University of Kent Medway Building', city: 'Chatham', county: 'Kent', country: 'United Kingdom', postcode: 'ME4 4AG', distance: 'Approx 1.5 miles away', tel: 'Tel: 01227 764000' }
    // Add more nearby centres as needed
  ];

  const handleFindCentre = () => {
    if (postcode.length > 8 || postcode.length < 7 || registration.trim() === '') {
      alert('Invalid postcode or registration');
    } else {
      // Filter the nearbyCentres based on the first two letters of the entered postcode
      const foundCentre = nearbyCentres.find((centre) => centre.postcode.startsWith(postcode.slice(0, 1)));
      // Update the selectedCentre state with the found centre information
      setSelectedCentre(foundCentre);
    }
  };

  const handleConfirm = () => {
    // Navigate to the service page with the values from the text fields
    window.location.href = `/service?postcode=${encodeURIComponent(postcode)}&registration=${encodeURIComponent(registration)}`;
  };

  return (
    <div className="py-24 sm:py-32">
      <div className="mx-auto max-w-2xl text-center">
        <h2 className="mt-2 text-4xl font-bold tracking-tight sm:text-6xl" style={{ color: '#112D4E' }}>
          YOUR DETAILS:
        </h2>

        <div className="mx-2">
          {/* Use text input for postcode */}
          <input
            type="text"
            value={postcode}
            onChange={(e) => setPostcode(e.target.value)}
            placeholder="Enter your postcode"
            className="block font-extralight"
            style={{ fontSize: '16px' }}
          />
        </div>

        <div className="mx-2 mt-4">
          {/* Use text input for registration */}
          <input
            type="text"
            value={registration}
            onChange={(e) => setRegistration(e.target.value)}
            placeholder="Enter your registration"
            className="block font-extralight"
            style={{ fontSize: '16px' }}
          />
        </div>

        <button
          className="text-white font-light rounded-full px-9 py-2 ml-5 mt-5"
          style={{ backgroundColor: '#112D4E', fontSize: '16px' }}
          onClick={handleFindCentre}
        >
          Find your centre
        </button>
      </div>

      <div className="mx-auto my-16 grid p-6 max-w-lg grid-cols-1 gap-6 sm:mt-20 lg:mx-auto lg:max-w-none lg:grid-cols-2 lg:gap-8 justify-center" style={{ color: '#112D4E' }}>
        {/* Canterbury Address */}
        <div className="p-8 rounded-xl bg/5" style={{ backgroundColor: '#DBE2EF' }}>
          <h2 className="text-xl font-bold mb-2">YOUR VEHICLE DETAILS </h2>
          <p style={{ fontWeight: 'bold' }}>
            Registration: {registration}
          </p>
          <p className="" style={{ color: '#3F72AF' }}>
            Make: {/* Add Make here */}
          </p>
          <p className="" style={{ color: '#3F72AF' }}>
            Model: {/* Add Model here */}
          </p>
        </div>

        {/* Selected Centre */}
        <div className="p-6 rounded-xl bg/3 text-center" style={{ backgroundColor: '#DBE2EF' }}>
          <h2 className="text-xl font-bold mb-2">YOUR SELECTED CENTRE</h2>
          <p style={{ fontWeight: 'bold' }}>
            <p></p>
          </p>
          {selectedCentre && (
            <div>
              <p className="" style={{ color: '#3F72AF' }}>
                {selectedCentre.address}
              </p>
              <p className="" style={{ color: '#3F72AF' }}>
                {selectedCentre.city}
              </p>
              <p className="" style={{ color: '#3F72AF' }}>
                {selectedCentre.county}
              </p>
              <p className="" style={{ color: '#3F72AF' }}>
                {selectedCentre.country}
              </p>
              <p className="" style={{ color: '#3F72AF' }}>
                {selectedCentre.postcode}
              </p>
              <p className="" style={{ color: '#3F72AF' }}>
                {selectedCentre.distance}
              </p>
              <p className="" style={{ color: '#3F72AF' }}>
                {selectedCentre.tel}
              </p>
            </div>
          )}

          <button className="text-white px-4 py-2 rounded mt-2" style={{ backgroundColor: '#112D4E' }} onClick={() => setShowNearbyCentres(!showNearbyCentres)}>
            {showNearbyCentres ? 'HIDE NEARBY CENTRES' : 'OTHER NEARBY CENTRES'}
          </button>

          {showNearbyCentres && (
            <div>
              {/* Render nearby centres here */}
              {nearbyCentres.map((centre, index) => (
                <div key={index}>
                  {/* Display centre information */}
                  {/* ... (similar to renderNearbyCentres function in your original code) */}
                </div>
              ))}
            </div>
          )}

          {/* Use Link component to navigate to the service page */}
          <button className="text-white px-4 py-2 rounded mt-2" style={{ backgroundColor: '#112D4E', display: 'block', marginLeft: 'auto' }} onClick={handleConfirm}>
            CONFIRM
          </button>
        </div>
      </div>
    </div>
  );
}

