import { json, LoaderFunction } from "@remix-run/node";
import { executeQuery } from "./database"; // Implement your database connection
const bcrypt = require("bcryptjs");

export let action: LoaderFunction = async ({ request }) => {
  if (request.method === "POST") {
    // Parse form data from the request
    const formData = new URLSearchParams(await request.text());

    const username = formData.get("username");
    const password = formData.get("password");

    const query = `SELECT * FROM comp6000_06.Staff WHERE username = ?`;

    try {
      // Query the database to find the user by email (username)
      const result = await executeQuery(query, [username]);

      if (result.length > 0) {
        // User found, check the password
        const user = result[0];
        const passwordMatch = await bcrypt.compare(password, user.password);

        if (passwordMatch) {
          // Password is correct, login successful
          return json({ message: "Login successful" }, { status: 302, headers: { Location: "/adminProfile" } });
        } else {
          // Password doesn't match
          return json({ error: "Invalid username or password" }, { status: 401 });
        }
      } else {
        // User not found
        return json({ error: "Invalid username or password" }, { status: 401 });
      }
    } catch (error) {
      // Handle database errors
      console.error(error);
      return json({ error: "Database error" }, { status: 500 });
    }
  }

  // Return an empty response for GET requests
  return json({});
};
